import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ExpensePostMultiInvoiceComponent } from './expense-post-multi-invoice.component';

export const routes: Routes = [
  { path: '', component: ExpensePostMultiInvoiceComponent },
  { path: ':id/:type', component: ExpensePostMultiInvoiceComponent },
  { path: ':id/:invoiceid', component: ExpensePostMultiInvoiceComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ExpensePostInvoiceRoutingModule { }
